/* Font Imports */
@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@0,100..800;1,100..800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Franklin+Gothic+Heavy&display=swap');

/* Global styles and CSS reset */
/* This file contains the main styling for our portfolio */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'JetBrains Mono', monospace;
  line-height: 1.6;
  color: #333;
  background-color: #f8f9fa;
}

/* Container for centering content */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Typography */
h1, h2, h3 {
  margin-bottom: 1rem;
}

h1 {
  font-size: 2.5rem;
  font-weight: 700;
}

h2 {
  font-size: 2rem;
  font-weight: 600;
}

h3 {
  font-size: 1.5rem;
  font-weight: 500;
}

p {
  margin-bottom: 1rem;
  color: #666;
}

/* Smooth scrolling for the whole page */
html {
  scroll-behavior: smooth;
}