/* Modern Portfolio Styles */
/* Inspired by contemporary design patterns with smooth animations */

/* Kanagawa Dragon Dark Theme */
:root {
  --primary-color: #c4746e;        /* Dragon Red */
  --primary-dark: #b8bb26;         /* Dragon Yellow */
  --secondary-color: #a6a69c;      /* Dragon Ash */
  --accent-color: #8ea4a2;         /* Dragon Aqua */
  --accent-light: #87a987;         /* Dragon Green */
  --text-primary: #c5c9c5;         /* Dragon White */
  --text-secondary: #a6a69c;       /* Dragon Ash */
  --bg-primary: #181616;           /* Dragon Black */
  --bg-secondary: #201d1d;         /* Dragon Black 2 */
  --bg-tertiary: #282727;          /* Dragon Black 3 */
  --success-color: #87a987;        /* Dragon Green */
  --warning-color: #c4746e;        /* Dragon Red */
  --bg-gradient: linear-gradient(135deg, #c4746e 0%, #8ea4a2 100%);
  --bg-gradient-light: linear-gradient(135deg, #87a987 0%, #b8bb26 100%);
  --bg-gradient-accent: linear-gradient(135deg, #8ea4a2 0%, #a6a69c 100%);
  --shadow-light: 0 2px 10px rgba(0, 0, 0, 0.3);
  --shadow-medium: 0 4px 20px rgba(0, 0, 0, 0.4);
  --shadow-heavy: 0 8px 30px rgba(0, 0, 0, 0.6);
  --border-radius: 12px;
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'JetBrains Mono', monospace;
  line-height: 1.6;
  color: var(--text-primary);
  background-color: var(--bg-primary);
  overflow-x: hidden;
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  line-height: 1.2;
}

/* Button Styles */
.btn {
  display: inline-block;
  padding: 12px 30px;
  border: none;
  border-radius: 50px;
  font-size: 16px;
  font-weight: 500;
  font-family: 'JetBrains Mono', monospace;
  text-decoration: none;
  cursor: pointer;
  transition: var(--transition);
  position: relative;
  overflow: hidden;
}

.btn-primary {
  background: var(--bg-gradient);
  color: white;
  box-shadow: var(--shadow-medium);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-heavy);
}

.btn-secondary {
  background: transparent;
  color: var(--primary-color);
  border: 2px solid var(--primary-color);
}

.btn-secondary:hover {
  background: var(--primary-color);
  color: white;
  transform: translateY(-2px);
}

/* Header Styles */
.header {
  background: rgba(24, 22, 22, 0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(166, 166, 156, 0.2);
  padding: 1rem 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  transition: var(--transition);
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
  margin: 0;
}

.portfolio-logo {
  background: var(--bg-gradient);
  color: white;
  padding: 8px 16px;
  border-radius: 50px;
  font-size: 0.8rem;
  font-weight: 800;
  font-family: 'Arial Black', 'Helvetica', sans-serif;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  gap: 6px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: none;
  cursor: pointer;
  text-decoration: none;
  box-shadow: 0 4px 15px rgba(196, 116, 110, 0.3);
  position: relative;
  overflow: hidden;
}

.portfolio-logo::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.6s ease;
}

.portfolio-logo:hover::before {
  left: 100%;
}

.portfolio-logo::after {
  content: '⚡';
  font-size: 0.8rem;
  opacity: 0;
  transform: translateX(-10px);
  transition: all 0.3s ease;
}

.portfolio-logo:hover {
  transform: translateY(-2px) scale(1.02);
  box-shadow: 0 8px 25px rgba(196, 116, 110, 0.4);
}

.portfolio-logo:hover::after {
  opacity: 1;
  transform: translateX(0);
}

.portfolio-logo:active {
  transform: translateY(0) scale(0.98);
  transition: all 0.1s ease;
}

.nav-list {
  display: flex;
  list-style: none;
  gap: 2rem;
}

.nav-link {
  background: none;
  border: none;
  color: var(--text-primary);
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  padding: 0.5rem 1rem;
  border-radius: 25px;
  transition: var(--transition);
  position: relative;
}

.nav-link:hover {
  color: var(--primary-color);
  background: rgba(196, 116, 110, 0.2);
}

/* Hero Section */
.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  padding: 120px 0 80px;
  background: linear-gradient(135deg, #181616 0%, #201d1d 100%);
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 80%, rgba(30, 64, 175, 0.05) 0%, transparent 50%),
              radial-gradient(circle at 80% 20%, rgba(14, 165, 233, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.hero::after {
  content: '';
  position: absolute;
  top: 20%;
  right: 10%;
  width: 100px;
  height: 100px;
  background-image: url("/paperAirplane.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.8;
  z-index: 1;
  pointer-events: none;
  animation: paperPlaneFloat 6s ease-in-out infinite;
  filter: brightness(0) saturate(100%) invert(77%) sepia(17%) saturate(1098%) hue-rotate(321deg) brightness(92%) contrast(87%);
  transition: filter 0.3s ease;
}

.hero::after:hover {
  filter: brightness(0) saturate(100%) invert(69%) sepia(69%) saturate(372%) hue-rotate(12deg) brightness(98%) contrast(92%);
  opacity: 1;
  transform: scale(1.1);
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.1;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="50" cy="50" r="30" fill="none" stroke="%23c4746e" stroke-width="0.5"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
}

.hero-quote {
  text-align: center;
  margin-bottom: 3rem;
  padding: 1.5rem;
  position: relative;
  z-index: 10;
}

.hero-quote blockquote {
  color: var(--text-secondary);
  font-size: 1.1rem;
  font-style: italic;
  margin: 0 0 0.5rem 0;
  line-height: 1.6;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.hero-quote cite {
  color: var(--primary-color);
  font-size: 0.9rem;
  font-weight: 500;
  display: block;
}

.blackbird-container {
  position: absolute;
  top: 15%;
  right: -10%;
  width: 300px;
  height: 150px;
  z-index: 2;
  opacity: 0.4;
  animation: blackbirdFlight 20s linear infinite;
}

.hero-blackbird {
  width: 100%;
  height: auto;
  filter: brightness(0) saturate(100%) invert(85%) sepia(8%) saturate(726%) hue-rotate(322deg) brightness(95%) contrast(88%);
  transform: rotate(-5deg);
  transition: all 0.3s ease;
}

.blackbird-container:hover .hero-blackbird {
  filter: brightness(0) saturate(100%) invert(69%) sepia(69%) saturate(372%) hue-rotate(12deg) brightness(98%) contrast(92%);
  transform: rotate(0deg) scale(1.1);
  opacity: 0.8;
}

@keyframes blackbirdFlight {
  0% {
    right: -15%;
    top: 15%;
    transform: translateY(0px);
  }
  25% {
    right: 75%;
    top: 10%;
    transform: translateY(-10px);
  }
  50% {
    right: 50%;
    top: 20%;
    transform: translateY(5px);
  }
  75% {
    right: 25%;
    top: 8%;
    transform: translateY(-8px);
  }
  100% {
    right: -15%;
    top: 15%;
    transform: translateY(0px);
  }
}

.floating-equations {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.equation {
  position: absolute;
  color: var(--primary-color);
  font-family: 'JetBrains Mono', monospace;
  font-size: 1rem;
  opacity: 0.8;
  font-weight: 500;
  letter-spacing: 0.5px;
  animation-duration: 15s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  text-shadow: 0 0 15px rgba(196, 116, 110, 0.5);
}

.equation-1 {
  top: 20%;
  left: 10%;
  animation: floatEquation1 18s linear infinite;
}

.equation-2 {
  top: 60%;
  right: 15%;
  animation: floatEquation2 22s linear infinite;
}

.equation-3 {
  top: 40%;
  left: 5%;
  animation: floatEquation3 20s linear infinite;
}

.equation-4 {
  top: 80%;
  right: 25%;
  animation: floatEquation4 16s linear infinite;
}

.equation-5 {
  top: 15%;
  right: 30%;
  animation: floatEquation5 24s linear infinite;
}

@keyframes floatEquation1 {
  0% { transform: translate(0, 0) rotate(0deg); opacity: 0.6; }
  25% { transform: translate(50px, -30px) rotate(2deg); opacity: 0.9; }
  50% { transform: translate(100px, 20px) rotate(-1deg); opacity: 0.7; }
  75% { transform: translate(150px, -10px) rotate(1deg); opacity: 0.6; }
  100% { transform: translate(200px, 0) rotate(0deg); opacity: 0.5; }
}

@keyframes floatEquation2 {
  0% { transform: translate(0, 0) rotate(0deg); opacity: 0.7; }
  25% { transform: translate(-40px, 30px) rotate(-2deg); opacity: 0.6; }
  50% { transform: translate(-80px, -20px) rotate(1deg); opacity: 0.8; }
  75% { transform: translate(-120px, 10px) rotate(-1deg); opacity: 0.6; }
  100% { transform: translate(-160px, 0) rotate(0deg); opacity: 0.5; }
}

@keyframes floatEquation3 {
  0% { transform: translate(0, 0) rotate(0deg); opacity: 0.6; }
  25% { transform: translate(60px, 40px) rotate(1deg); opacity: 0.7; }
  50% { transform: translate(120px, -30px) rotate(-2deg); opacity: 0.8; }
  75% { transform: translate(180px, 20px) rotate(1deg); opacity: 0.6; }
  100% { transform: translate(240px, 0) rotate(0deg); opacity: 0.5; }
}

@keyframes floatEquation4 {
  0% { transform: translate(0, 0) rotate(0deg); opacity: 0.8; }
  25% { transform: translate(-30px, -40px) rotate(-1deg); opacity: 0.6; }
  50% { transform: translate(-60px, 30px) rotate(2deg); opacity: 0.7; }
  75% { transform: translate(-90px, -20px) rotate(-1deg); opacity: 0.6; }
  100% { transform: translate(-120px, 0) rotate(0deg); opacity: 0.5; }
}

@keyframes floatEquation5 {
  0% { transform: translate(0, 0) rotate(0deg); opacity: 0.7; }
  25% { transform: translate(-50px, 35px) rotate(2deg); opacity: 0.8; }
  50% { transform: translate(-100px, -25px) rotate(-1deg); opacity: 0.6; }
  75% { transform: translate(-150px, 15px) rotate(1deg); opacity: 0.7; }
  100% { transform: translate(-200px, 0) rotate(0deg); opacity: 0.5; }
}

.hero-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  position: relative;
  z-index: 10;
}

.hero-title {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  font-weight: 700;
}

.hero-greeting {
  font-size: 1.5rem;
  color: var(--text-secondary);
  font-weight: 400;
  margin-bottom: 0.5rem;
}

.hero-name {
  background: var(--bg-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
}

.hero-subtitle {
  font-size: 1.8rem;
  color: var(--primary-color);
  margin-bottom: 1.5rem;
  font-weight: 500;
  position: relative;
}

.typing-text {
  position: relative;
}

.typing-text::after {
  content: '|';
  animation: blink 1s infinite;
  color: var(--primary-color);
}

@keyframes blink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0; }
}

.hero-description {
  font-size: 1.2rem;
  color: var(--text-secondary);
  margin-bottom: 2.5rem;
  line-height: 1.8;
}

.hero-buttons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.hero-image {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.image-container {
  position: relative;
  z-index: 2;
}

.profile-image-link {
  position: relative;
  display: block;
  border-radius: 50%;
  overflow: hidden;
  transition: var(--transition);
  cursor: pointer;
}

.hero-profile-image {
  width: 350px;
  height: 350px;
  border-radius: 50%;
  object-fit: cover;
  border: 6px solid white;
  box-shadow: var(--shadow-heavy);
  transition: all 0.4s ease;
  display: block;
}

.profile-image-link:hover .hero-profile-image {
  transform: scale(1.05);
  filter: brightness(0.8);
}

.instagram-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 12px 16px;
  border-radius: 25px;
  display: flex;
  align-items: center;
  gap: 8px;
  opacity: 0;
  transition: all 0.3s ease;
  pointer-events: none;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.profile-image-link:hover .instagram-overlay {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}

.instagram-icon {
  width: 20px;
  height: 20px;
  color: white;
  transition: color 0.3s ease;
}

.profile-image-link:hover .instagram-icon {
  color: #E4405F;
}

.instagram-text {
  font-family: 'JetBrains Mono', monospace;
  font-weight: 600;
  font-size: 0.9rem;
  letter-spacing: 0.5px;
}

.image-decoration {
  position: absolute;
  top: -20px;
  right: -20px;
  width: 100px;
  height: 100px;
  background: var(--bg-gradient-accent);
  border-radius: 50%;
  opacity: 0.8;
  z-index: -1;
  animation: float 3s ease-in-out infinite;
}

.image-decoration::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 60px;
  background: var(--bg-gradient);
  border-radius: 50%;
  opacity: 0.6;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

@keyframes paperPlaneFloat {
  0%, 100% { 
    transform: translateY(0px) translateX(0px) rotate(0deg); 
    opacity: 0.8; 
  }
  33% { 
    transform: translateY(-15px) translateX(10px) rotate(5deg); 
    opacity: 0.6; 
  }
  66% { 
    transform: translateY(-5px) translateX(-5px) rotate(-3deg); 
    opacity: 0.9; 
  }
}

.hero-scroll-indicator {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  z-index: 10;
}

/* Simple paper airplane effect */

.scroll-arrow {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.scroll-arrow span {
  display: block;
  width: 2px;
  height: 20px;
  background: var(--primary-color);
  margin: 2px 0;
  animation: scrollAnimation 2s infinite;
}

.scroll-arrow span:nth-child(2) {
  animation-delay: 0.2s;
}

.scroll-arrow span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes scrollAnimation {
  0%, 100% { opacity: 0.3; transform: translateY(0); }
  50% { opacity: 1; transform: translateY(10px); }
}

/* Section Styles */
section {
  padding: 80px 0;
}

.section-header {
  text-align: center;
  margin-bottom: 4rem;
}

.section-title {
  font-size: 2.5rem;
  color: var(--text-primary);
  margin-bottom: 1rem;
  position: relative;
  font-weight: 600;
}

.section-title::after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background: var(--bg-gradient);
  margin: 1rem auto;
  border-radius: 2px;
}

.section-subtitle {
  font-size: 1.2rem;
  color: var(--text-secondary);
  max-width: 600px;
  margin: 0 auto;
}

/* About Section */
.about {
  background: var(--bg-secondary);
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  margin-bottom: 4rem;
}

.about-story h3 {
  color: var(--primary-color);
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
}

.about-story p {
  font-size: 1.1rem;
  line-height: 1.8;
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
}

.achievements-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

.achievement-card {
  background: var(--bg-secondary);
  padding: 1.5rem;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-light);
  border: 1px solid rgba(166, 166, 156, 0.1);
  transition: var(--transition);
}

.achievement-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-medium);
}

.achievement-icon {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.achievement-card h4 {
  color: var(--primary-color);
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
}

.achievement-card p {
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.achievement-card.clickable {
  transition: all 0.3s ease;
  position: relative;
}

.achievement-card.clickable:hover {
  background: var(--bg-gradient);
  color: white;
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(196, 116, 110, 0.3);
}

.paper-link-indicator {
  margin-top: 0.5rem;
  font-size: 0.8rem;
  color: var(--primary-color);
  font-weight: 500;
  opacity: 0.8;
}

.achievement-card.clickable:hover .paper-link-indicator {
  color: white;
  opacity: 1;
}

.achievement-card.clickable:hover h4,
.achievement-card.clickable:hover p {
  color: white;
}

.skills-section h3 {
  text-align: center;
  color: var(--primary-color);
  margin-bottom: 2rem;
  font-size: 1.8rem;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
}

.skill-card {
  background: var(--bg-secondary);
  padding: 1rem;
  border-radius: var(--border-radius);
  text-align: center;
  font-weight: 500;
  color: var(--primary-color);
  box-shadow: var(--shadow-light);
  border: 1px solid rgba(166, 166, 156, 0.1);
  transition: var(--transition);
}

.skill-card:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-medium);
  background: var(--primary-color);
  color: var(--bg-primary);
}

.skill-icon {
  display: inline-block;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
}

.skill-logo {
  width: 24px;
  height: 24px;
  margin-bottom: 0.5rem;
}

.skill-logo svg {
  width: 100%;
  height: 100%;
  transition: var(--transition);
}

.skill-card:hover .skill-logo svg {
  color: var(--bg-primary);
}

.skill-name {
  display: block;
  font-size: 0.9rem;
  font-weight: 500;
}

/* Experience Section */
.experience {
  background: var(--bg-primary);
}

.experience-content {
  margin-bottom: 3rem;
}

.experience-card {
  background: var(--bg-secondary);
  padding: 2rem;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-light);
  border: 1px solid rgba(166, 166, 156, 0.1);
  margin-bottom: 2rem;
}

.experience-header {
  margin-bottom: 1.5rem;
}

.experience-title {
  color: var(--primary-color);
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.experience-meta {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.company {
  font-weight: 500;
  color: var(--accent-color);
}

.location, .period {
  color: var(--text-secondary);
}

.experience-description {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
}

.responsibilities {
  margin-bottom: 1.5rem;
}

.responsibilities h4 {
  color: var(--text-primary);
  margin-bottom: 0.8rem;
  font-size: 1.1rem;
}

.responsibilities ul {
  list-style: none;
  padding-left: 0;
}

.responsibilities li {
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
  padding-left: 1.2rem;
  position: relative;
  line-height: 1.5;
}

.responsibilities li::before {
  content: '▸';
  color: var(--primary-color);
  position: absolute;
  left: 0;
  font-weight: bold;
}

.responsibility-section {
  margin-bottom: 1.5rem;
}

.responsibility-category {
  color: var(--primary-color);
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.8rem;
  border-bottom: 2px solid var(--primary-color);
  padding-bottom: 0.3rem;
  display: inline-block;
}

.responsibility-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.responsibility-list li {
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
  padding-left: 1.2rem;
  position: relative;
  line-height: 1.5;
}

.responsibility-list li::before {
  content: '▸';
  color: var(--primary-color);
  position: absolute;
  left: 0;
  font-weight: bold;
}

.experience-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.current-projects {
  margin-top: 3rem;
}

.projects-subtitle {
  color: var(--primary-color);
  font-size: 1.8rem;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 600;
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.project-status {
  background: var(--bg-gradient-light);
  color: var(--bg-primary);
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
}

.project-responsibilities {
  margin-bottom: 1.5rem;
}

.project-responsibilities h5 {
  color: var(--text-primary);
  margin-bottom: 0.8rem;
  font-size: 1rem;
}

.project-responsibilities ul {
  list-style: none;
  padding-left: 0;
}

.project-responsibilities li {
  color: var(--text-secondary);
  margin-bottom: 0.4rem;
  padding-left: 1.2rem;
  position: relative;
  line-height: 1.5;
  font-size: 0.95rem;
}

.project-responsibilities li::before {
  content: '▸';
  color: var(--accent-color);
  position: absolute;
  left: 0;
  font-weight: bold;
}

/* Projects Section */
.projects {
  background: var(--bg-secondary);
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
}

.project-card {
  background: var(--bg-secondary);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: var(--shadow-light);
  border: 1px solid rgba(166, 166, 156, 0.1);
  transition: var(--transition);
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-heavy);
}

.project-content {
  padding: 2rem;
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.project-meta {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0.3rem;
}

.project-period {
  font-size: 0.85rem;
  color: var(--text-secondary);
  font-weight: 500;
}

.project-title {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 1.4rem;
}

.project-status {
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  white-space: nowrap;
}

.project-status.ongoing {
  background: var(--bg-gradient);
  color: white;
}

.project-status.ongoing.with-date {
  background: var(--bg-gradient-accent);
  color: white;
}

.project-status.completed {
  background: rgba(135, 169, 135, 0.2);
  color: var(--success-color);
  border: 1px solid var(--success-color);
}

.project-status.not-started {
  background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 100%);
  color: white;
}

/* Gantt Chart Styles */
.gantt-chart-section {
  margin-top: 3rem;
  padding: 2rem;
  background: var(--bg-secondary);
  border-radius: var(--border-radius);
  border: 1px solid rgba(166, 166, 156, 0.1);
}

.gantt-title {
  color: var(--primary-color);
  margin-bottom: 2rem;
  text-align: center;
  font-size: 1.5rem;
}

.gantt-chart {
  overflow-x: auto;
  background: var(--bg-primary);
  border-radius: var(--border-radius);
  padding: 1rem;
}

.gantt-header {
  margin-bottom: 1rem;
}

.gantt-months {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 1px;
  background: rgba(166, 166, 156, 0.1);
  padding: 0.5rem;
  border-radius: 6px;
}

.gantt-month {
  text-align: center;
  font-size: 0.8rem;
  font-weight: 500;
  color: var(--text-secondary);
  padding: 0.5rem;
}

.gantt-row {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  min-height: 40px;
}

.gantt-label {
  width: 250px;
  padding-right: 1rem;
  font-size: 0.9rem;
  color: var(--text-primary);
  font-weight: 500;
  flex-shrink: 0;
}

.gantt-label.milestone {
  color: var(--accent-color);
  font-style: italic;
}

.gantt-timeline {
  flex: 1;
  position: relative;
  height: 30px;
  background: rgba(166, 166, 156, 0.1);
  border-radius: 15px;
}

.gantt-bar {
  position: absolute;
  height: 100%;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: 500;
  color: white;
  transition: var(--transition);
}

.gantt-bar.ongoing {
  background: var(--bg-gradient);
}

.gantt-bar.completed {
  background: linear-gradient(135deg, var(--success-color) 0%, #87a987 100%);
}

.gantt-bar.ongoing-indefinite {
  background: repeating-linear-gradient(
    45deg,
    var(--primary-color),
    var(--primary-color) 10px,
    var(--accent-color) 10px,
    var(--accent-color) 20px
  );
  animation: ongoingShimmer 2s ease-in-out infinite;
}

@keyframes ongoingShimmer {
  0%, 100% { opacity: 0.8; }
  50% { opacity: 1; }
}

.gantt-bar-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 0.5rem;
}

.gantt-milestone {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}

.milestone-diamond {
  font-size: 1.5rem;
  color: var(--accent-color);
  text-shadow: 0 0 10px rgba(142, 164, 162, 0.5);
}

.milestone-text {
  font-size: 0.7rem;
  color: var(--accent-color);
  margin-top: 0.25rem;
  white-space: nowrap;
  background: var(--bg-primary);
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
  border: 1px solid var(--accent-color);
}

.gantt-milestone:hover .milestone-diamond {
  transform: scale(1.2);
  color: var(--primary-color);
}

.gantt-milestone:hover .milestone-text {
  background: var(--accent-color);
  color: var(--bg-primary);
}

.project-description {
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.tech-tag {
  background: rgba(196, 116, 110, 0.2);
  color: var(--primary-color);
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 500;
}

.project-links {
  display: flex;
  gap: 1rem;
}

.project-link {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
  padding: 0.5rem 1.5rem;
  border: 2px solid var(--primary-color);
  border-radius: 25px;
  transition: var(--transition);
  font-size: 0.9rem;
}

.project-link:hover {
  background: var(--primary-color);
  color: white;
  transform: translateY(-2px);
}

.project-link-disabled {
  color: #999;
  font-weight: 500;
  padding: 0.5rem 1.5rem;
  border: 2px solid #ddd;
  border-radius: 25px;
  cursor: not-allowed;
  font-size: 0.9rem;
}

/* Contact Section */
.contact {
  background: var(--bg-secondary);
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: start;
}

.contact-info p {
  font-size: 1.1rem;
  line-height: 1.8;
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

.contact-links {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact-link {
  color: var(--primary-color);
  text-decoration: none;
  font-size: 1.1rem;
  transition: var(--transition);
  padding: 0.5rem 0;
}

.contact-link:hover {
  color: var(--primary-dark);
  transform: translateX(5px);
}

/* Contact Compact Styling */
.contact-compact {
  padding: 60px 0;
}

.contact-links-compact {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  max-width: 800px;
  margin: 0 auto;
}

.contact-links-compact .contact-link {
  color: var(--primary-color);
  text-decoration: none;
  font-size: 1rem;
  transition: var(--transition);
  padding: 0.75rem 1.5rem;
  background: var(--bg-primary);
  border-radius: 8px;
  border: 1px solid rgba(196, 116, 110, 0.2);
  white-space: nowrap;
}

.contact-links-compact .contact-link:hover {
  color: var(--primary-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow-light);
}

/* Authentication Styles */
.login-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.login-modal {
  background: var(--bg-secondary);
  border-radius: var(--border-radius);
  padding: 2rem;
  width: 100%;
  max-width: 400px;
  box-shadow: var(--shadow-heavy);
  border: 1px solid rgba(196, 116, 110, 0.2);
}

.login-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(196, 116, 110, 0.2);
}

.login-header h3 {
  color: var(--primary-color);
  margin: 0;
  font-size: 1.3rem;
}

.close-btn {
  background: none;
  border: none;
  color: var(--text-secondary);
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: var(--transition);
}

.close-btn:hover {
  background: rgba(196, 116, 110, 0.1);
  color: var(--primary-color);
}

.login-form,
.password-change-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.login-form .form-group,
.password-change-form .form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.login-form label,
.password-change-form label {
  color: var(--text-primary);
  font-weight: 500;
  font-size: 0.9rem;
}

.login-form input,
.password-change-form input {
  padding: 0.75rem;
  border: 1px solid rgba(166, 166, 156, 0.3);
  border-radius: 6px;
  background: var(--bg-primary);
  color: var(--text-primary);
  font-family: 'JetBrains Mono', monospace;
  transition: var(--transition);
}

.login-form input:focus,
.password-change-form input:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(196, 116, 110, 0.2);
}

.login-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.login-btn,
.change-password-btn,
.cancel-btn,
.access-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  font-family: 'JetBrains Mono', monospace;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
  flex: 1;
}

.login-btn,
.access-btn {
  background: var(--bg-gradient);
  color: white;
}

.login-btn:hover,
.access-btn:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
}

.change-password-btn,
.cancel-btn {
  background: transparent;
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.change-password-btn:hover,
.cancel-btn:hover {
  background: var(--primary-color);
  color: white;
}

.error-message {
  color: var(--warning-color);
  font-size: 0.9rem;
  text-align: center;
  padding: 0.5rem;
  background: rgba(196, 116, 110, 0.1);
  border-radius: 4px;
  border: 1px solid rgba(196, 116, 110, 0.2);
}

.success-message {
  color: var(--success-color);
  font-size: 0.9rem;
  text-align: center;
  padding: 0.5rem;
  background: rgba(135, 169, 135, 0.1);
  border-radius: 4px;
  border: 1px solid rgba(135, 169, 135, 0.2);
}

/* Protected Content Styles */
.protected-content {
  position: relative;
}

.blurred {
  filter: blur(8px);
  pointer-events: none;
  user-select: none;
}

.login-prompt {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  background: var(--bg-secondary);
  padding: 2rem;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-heavy);
  border: 1px solid rgba(196, 116, 110, 0.2);
  text-align: center;
  min-width: 300px;
}

.login-prompt-content h4 {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.login-prompt-content p {
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.projects-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.logout-btn {
  background: transparent;
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
  padding: 0.5rem 1rem;
  border-radius: 4px;
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.9rem;
  cursor: pointer;
  transition: var(--transition);
}

.logout-btn:hover {
  background: var(--primary-color);
  color: white;
}

.contact-form {
  background: var(--bg-secondary);
  padding: 2.5rem;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-light);
  border: 1px solid rgba(166, 166, 156, 0.1);
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
  font-weight: 500;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0.8rem;
  border: 2px solid rgba(166, 166, 156, 0.2);
  border-radius: var(--border-radius);
  font-size: 1rem;
  background: var(--bg-tertiary);
  color: var(--text-primary);
  transition: var(--transition);
  font-family: 'JetBrains Mono', monospace;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(196, 116, 110, 0.2);
}

.submit-btn {
  background: var(--bg-gradient);
  color: white;
  padding: 0.8rem 2rem;
  border: none;
  border-radius: 25px;
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition);
  font-weight: 500;
}

.submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
}

.success-message {
  background: rgba(135, 169, 135, 0.2);
  color: var(--success-color);
  padding: 1rem;
  border-radius: var(--border-radius);
  margin-bottom: 1rem;
  border: 1px solid rgba(135, 169, 135, 0.3);
}

/* Footer */
.footer {
  background: var(--bg-tertiary);
  color: var(--text-secondary);
  text-align: center;
  padding: 2rem 0;
  border-top: 1px solid rgba(166, 166, 156, 0.1);
}

.footer p {
  margin: 0;
  color: var(--text-secondary);
}

/* Responsive Design */
@media (max-width: 768px) {
  .container {
    padding: 0 15px;
  }
  
  .header .container {
    flex-direction: column;
    gap: 1rem;
    padding: 1rem 15px;
  }
  
  .portfolio-logo {
    font-size: 0.7rem;
    padding: 6px 12px;
    letter-spacing: 0.6px;
  }
  
  .portfolio-logo::after {
    font-size: 0.7rem;
  }
  
  .nav {
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    scrollbar-width: none;
    -ms-overflow-style: none;
  }
  
  .nav::-webkit-scrollbar {
    display: none;
  }
  
  .nav-list {
    gap: 1rem;
    min-width: max-content;
    padding: 0 1rem;
    justify-content: flex-start;
  }
  
  .hero {
    padding: 140px 0 60px;
  }
  
  .hero-content {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 2rem;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.4rem;
  }
  
  .hero-profile-image {
    width: 250px;
    height: 250px;
  }
  
  .about-content,
  .contact-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .achievements-grid {
    grid-template-columns: 1fr;
  }
  
  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
  }
  
  section {
    padding: 60px 0;
  }
}

/* Extra small mobile devices */
@media (max-width: 480px) {
  .container {
    padding: 0 10px;
  }
  
  .nav-list {
    gap: 0.8rem;
    padding: 0 0.5rem;
  }
  
  .nav-link {
    font-size: 0.9rem;
    padding: 0.4rem 0.8rem;
    white-space: nowrap;
  }
  
  .hero-title {
    font-size: 2rem;
  }
  
  .hero-subtitle {
    font-size: 1.2rem;
  }
  
  .hero-profile-image {
    width: 200px;
    height: 200px;
  }
  
  .portfolio-logo {
    font-size: 0.6rem;
    padding: 5px 10px;
  }
  
  .hero-buttons {
    flex-direction: column;
    gap: 1rem;
  }
  
  .btn {
    width: 100%;
    text-align: center;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .hero-buttons {
    justify-content: center;
  }
  
  .btn {
    padding: 10px 20px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 2rem;
  }
  
  .hero-profile-image {
    width: 200px;
    height: 200px;
  }
  
  .contact-form {
    padding: 1.5rem;
  }
  
  .achievement-card,
  .project-card {
    margin: 0 10px;
  }
}

/* Education Section Styles */
.education-section {
  margin-bottom: 4rem;
}

.subsection-title {
  color: var(--primary-color);
  font-size: 1.8rem;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 600;
}

.education-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 2rem;
}

.education-card {
  background: var(--bg-secondary);
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid rgba(196, 116, 110, 0.1);
  display: flex;
  gap: 1.5rem;
}

.education-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15);
}

.education-icon {
  font-size: 2rem;
  color: var(--primary-color);
  flex-shrink: 0;
}

.education-content {
  flex: 1;
}

.education-degree {
  color: var(--text-primary);
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.education-institution {
  color: var(--primary-color);
  font-size: 1rem;
  margin-bottom: 0.3rem;
  font-weight: 500;
}

.education-year {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 0.8rem;
}

.education-description {
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 0.9rem;
}

.projects-section {
  margin-top: 2rem;
}

/* Goals Section Styles */
.goals {
  background: var(--bg-primary);
}

.goals-section {
  margin-top: 4rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(196, 116, 110, 0.2);
}

.goals-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
}

.goal-card {
  background: var(--bg-secondary);
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid rgba(196, 116, 110, 0.1);
  display: flex;
  gap: 1.5rem;
  position: relative;
  overflow: hidden;
}

.goal-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15);
}

.goal-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, var(--primary-color), var(--primary-dark));
}

.goal-icon {
  font-size: 2.5rem;
  color: var(--primary-color);
  flex-shrink: 0;
}

.goal-content {
  flex: 1;
}

.goal-title {
  color: var(--text-primary);
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.goal-timeframe {
  color: var(--primary-color);
  font-size: 0.9rem;
  margin-bottom: 1rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.goal-description {
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 0.95rem;
}

/* Timeline Section Styles */
.timeline-section {
  background: var(--bg-secondary);
  padding: 100px 0;
  position: relative;
}

.timeline {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem 0;
}

.timeline-line {
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 3px;
  background: linear-gradient(180deg, var(--primary-color), var(--primary-dark));
  transform: translateX(-50%);
  z-index: 1;
}

.timeline-item {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 4rem;
  opacity: 0;
  transform: translateY(80px) scale(0.9);
  transition: all 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.timeline-item.visible {
  opacity: 1;
  transform: translateY(0) scale(1);
  animation: expandTimeline 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
  animation-delay: var(--delay);
}

.timeline-item.visible .timeline-marker {
  animation: popIn 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55) forwards;
  animation-delay: calc(var(--delay) + 0.2s);
}

.timeline-item.visible .timeline-content {
  animation: slideInContent 0.7s ease-out forwards;
  animation-delay: calc(var(--delay) + 0.3s);
}

.timeline-item:nth-child(even) {
  flex-direction: row-reverse;
}

.timeline-item:nth-child(even) .timeline-content {
  text-align: right;
  padding-right: 3rem;
  padding-left: 0;
}

.timeline-item:nth-child(odd) .timeline-content {
  text-align: left;
  padding-left: 3rem;
  padding-right: 0;
}

.timeline-marker {
  position: absolute;
  left: 50%;
  transform: translateX(-50%) scale(0);
  z-index: 2;
  background: var(--bg-primary);
  border-radius: 50%;
  padding: 0.5rem;
  box-shadow: 0 0 0 4px var(--bg-secondary), 0 0 0 8px var(--primary-color);
  transition: transform 0.3s ease;
}

.timeline-icon {
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: var(--primary-color);
  border-radius: 50%;
  color: var(--bg-primary);
}

.timeline-content {
  flex: 1;
  max-width: 45%;
  background: var(--bg-primary);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(196, 116, 110, 0.1);
  position: relative;
  opacity: 0;
  transform: translateX(-20px);
  transition: all 0.4s ease;
}

.timeline-content::before {
  content: '';
  position: absolute;
  top: 50%;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  transform: translateY(-50%);
}

.timeline-item:nth-child(odd) .timeline-content::before {
  left: -20px;
  border-right-color: var(--bg-primary);
}

.timeline-item:nth-child(even) .timeline-content::before {
  right: -20px;
  border-left-color: var(--bg-primary);
}

.timeline-year {
  color: var(--primary-color);
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 0.5rem;
}

.timeline-title {
  color: var(--text-primary);
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.timeline-institution {
  color: var(--primary-dark);
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 1rem;
}

.timeline-description {
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 0.95rem;
}

.timeline-item.education .timeline-content {
  border-left: 4px solid var(--primary-color);
}

.timeline-item.career .timeline-content {
  border-left: 4px solid var(--primary-dark);
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes expandTimeline {
  0% {
    opacity: 0;
    transform: translateY(80px) scale(0.9);
  }
  50% {
    opacity: 0.5;
    transform: translateY(20px) scale(0.95);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes popIn {
  0% {
    transform: translateX(-50%) scale(0);
  }
  50% {
    transform: translateX(-50%) scale(1.2);
  }
  100% {
    transform: translateX(-50%) scale(1);
  }
}

@keyframes slideInContent {
  0% {
    opacity: 0;
    transform: translateX(-30px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Special animation for even items (right side) */
.timeline-item:nth-child(even).visible .timeline-content {
  animation: slideInContentRight 0.7s ease-out forwards;
  animation-delay: calc(var(--delay) + 0.3s);
}

@keyframes slideInContentRight {
  0% {
    opacity: 0;
    transform: translateX(30px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (max-width: 768px) {
  .education-grid {
    grid-template-columns: 1fr;
  }
  
  .education-card {
    padding: 1.5rem;
  }
  
  .goals-grid {
    grid-template-columns: 1fr;
  }
  
  .goal-card {
    padding: 1.5rem;
  }
  
  .timeline-line {
    left: 30px;
  }
  
  .timeline-item {
    flex-direction: column !important;
    align-items: flex-start;
  }
  
  .timeline-item:nth-child(even) .timeline-content,
  .timeline-item:nth-child(odd) .timeline-content {
    text-align: left;
    padding: 1.5rem;
    padding-left: 4rem;
    padding-right: 1.5rem;
    max-width: 100%;
    margin-left: 60px;
  }
  
  .timeline-marker {
    left: 30px;
  }
  
  .timeline-content::before {
    display: none;
  }
  
  .blackbird-container {
    width: 200px;
    height: 100px;
    top: 10%;
    opacity: 0.2;
  }
  
  .blackbird-container:hover .hero-blackbird {
    transform: rotate(0deg) scale(1.05);
  }
  
  .equation {
    font-size: 0.7rem;
    opacity: 0.1;
  }
  
  .floating-equations {
    display: none;
  }
}